import * as actionTypes from './actionTypes';

//action creator
export const saveResult = res => {
  // const updatedResult = res * 2;
  return {
    type: actionTypes.STORE_RESULT,
    result: res,
  };
};

export const storeStart = () => {
  return {
    type: actionTypes.STORE_START,
  };
};
export const storeFinish = () => {
  return {
    type: actionTypes.STORE_FINISH,
  };
};

//redux-thunk middlewareaction creator
//old actionblock()dispatch
export const storeResult = res => {
  //redux-thunkdispatchgetStateaction-creator
  //global stateaction-creatorgetStatecomponentmapDispatchToPropsglobal statebest-practice
  return (dispatch, getState) => {
    dispatch(storeStart());
    setTimeout(() => {
      const oldCounter = getState().ctr.counter;
      console.log(oldCounter);
      dispatch(saveResult(res));
      dispatch(storeFinish());
    }, 2000);
  };
};
export const deleteResult = resElId => {
  return {
    type: actionTypes.DELETE_RESULT,
    resultElId: resElId,
  };
};



