import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';

const initialState = {
  results: [],
  loading: false,
};

//switchlogicswitch
const deleteResult = (state, action) => {
  const updatedArray = state.results.filter(
    result => result.id !== action.resultElId
  );
  return updateObject(state, { results: updatedArray });
};

//reducerstateupdateimmutable(setState)
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.STORE_RESULT:
      return updateObject(state, {
        results: state.results.concat({
          id: new Date(),
          value: action.result * 2,
        }),
      });

    // return{
    //     ...state,
    //     //Array.pushoriginalimmutableconcat
    //     results:state.results.concat({id:new Date(), value: action.result*2})
    //     // counterlocalglobal state(action)
    // }
    case actionTypes.DELETE_RESULT:
      // const id = 2;
      // const newArray = [...state.results];//array
      // newArray.splice(id,1);//splicemutable
      // const updatedArray = state.results.filter(result=> result.id !== action.resultElId);
      //filterimmutable,actionid
      // return updateObject(state,{results:updatedArray});
      // return{
      //     ...state,
      //     //Array.pushoriginalimmutableconcat
      //     results:updatedArray
      // }
      return deleteResult(state, action);
    case actionTypes.STORE_START:
      return updateObject(state, {
        loading: true,
      });
    case actionTypes.STORE_FINISH:
      return updateObject(state, {
        loading: false,
      });
  }

  return state;
};

export default reducer;



